.footer {
  background-color: #fefefe;
  box-shadow: 0px -4px 24px rgba(105, 105, 105, 0.1);
}

.footer-nav__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
}

.footer-subscribe__title {
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #2c2c2c;
}

.footer-subscribe__input {
  font-weight: 400;
  font-size: 12px;
  line-height: 150%;
  color: #505050;
}

.footer-subscribe__btn {
  font-weight: 400;
  font-size: 13.6667px;
  line-height: 150%;
  color: #2c2c2c;
}

.footer-subscribe__agreement {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #2c2c2c;
}

.agreement-link {
  text-decoration-line: underline;
  color: #7eb101;
}

.footer-category__link {
  font-size: 12px;
  line-height: 14px;
  text-decoration-line: underline;
  color: #7eb101;
}

.footer-copyright__item {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #2c2c2c;
}
